<template>
  <div>
    <button class="ui button" :class="cClass" @click="outClick()">
      <template v-if="animated">
        <!-- å¿é¡»å¤æ­æ¯å¦æå¨ç»ï¼å¦ææï¼è¿éå¤æ­æ¯åªç§å¨ç» -->
        <!-- $slots:å¯ä»¥è·å¾ææçå¨ç»ç±»å -->
        <div v-if="$slots.hidden" class="hidden content">
          <slot name="hidden"></slot>
        </div>
        <div v-if="$slots.visible" class="visible content">
          <slot name="visible"></slot>
        </div>
      </template>
      <template v-else>
        <i v-if="icon" class="icon" :class="icon"></i>
        <slot>ææ¯é»è®¤çåå®¹</slot>
      </template>
    </button>
  </div>
</template>

<script type="text/javascript">
export default {
  name: 'MyButton',
  props: {
    size: {
      type: String,
      default: 'medium',
      validator (val) {
        // console.log(val) // 'mini', 'tiny', 'small', 'medium'.....
        // åªè¦ä¼ å¥ sizeå±æ§ï¼å°±ä¼è¿å¥å°è¿ä¸ªå½æ°ä¸­ï¼å¦è¿åtrue,åè¡¨ç¤ºçæ,å¦è¿åfalseï¼åè¡¨ç¤ºä¸åè®¸
        // å¦æä¼ å¥çsizeæ¯ï¼mini,medium,huge,massive.... å°±ok,å¦åå°±è¿åfalse
        return [
          'mini',
          'tiny',
          'small',
          'medium',
          'large',
          'big',
          'huge',
          'massive'
        ].includes(val)
      }
    },
    icon: {
      type: String,
      required: false
    },
    bkgColor: {
      type: String,
      required: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    animated: {
      type: String,
      default: ''
    }
  },
  created () {
    // console.log(this.$slots)  // å¯ä»¥è·å¾ææçå¨ç»ç±»å
  },
  computed: {
    cClass () {
      var classArr = [this.size]
      this.bkgColor && classArr.push('inverted' + ' ' + this.bkgColor)
      this.disabled && classArr.push('disabled')
      this.loading && classArr.push('loading')
      this.animated && classArr.push(`animated ${this.animated}`)
      return classArr.join(' ')
    }
  },
  data () {
    return {}
  },
  methods: {
    outClick () {
      this.$emit('click')
    }
  }
}
</script>

