<template>
  <div class="slider" @mouseenter="mouseEnter" @mouseleave="mouseLeave" @click="hClick">
    <div class="slider-content">
      <!-- è½®æ­å¾å¨åæ¢æ¶ï¼ä½¿ç¨vue æä¾çå¨ç»æ¹æ¡(transition-group)ï¼è®©åæ¢è¿ç¨æ´æ¸©åä¸äº -->
      <transition-group name="fade">
        <div
          class="slider-item"
          v-show="index===currentindex"
          v-for="(item,index) in list"
          :key="item.url"
        >
          <img :src="item.url" :alt="item.alt" />
        </div>
      </transition-group>
    </div>
    <!-- å·¦å³æé® -->
    <span class="btn btn_left" @click="hPrev"></span>
    <span class="btn btn_right" @click="hNext"></span>

    <!-- å·¦ä¾§æ é¢åºå -->
    <div class="txt">{{list[currentindex].alt}}</div>

    <!-- å°åç¹åºå -->
    <ol class="indirector">
      <li
        v-for="(item,index) in list"
        :key="index"
        :class="{current:index===currentindex}"
        @mouseenter="currentindex=index"
      ></li>

      <!-- <li class="current"></li> -->
    </ol>
  </div>
</template>

<script>
export default {
  name: 'MySlider',
  props: {
    // è½®æ­åè¡¨
    list: {
      type: Array,
      required: true
    },
    // å½åå¼å§æ­æ¾æ¯åªä¸å¼ å¾
    curIdx: {
      type: Number,
      default: 0,
      required: false
    },
    // èªå¨æ­æ¾çæ¯«ç§æ°
    auto: {
      type: Number,
      required: false,
      default: 0 // é»è®¤ä¸å¼å¯èªå¨æ­æ¾
    }
  },
  data () {
    return {
      // èªå®ä¹æ°æ®é¡¹æ¥æ¥æ¶curIdxå±æ§ã
      // å ä¸ºå¨ç»ä»¶åé¨ï¼ä¸åè®¸æ¹propså¼ç
      currentindex: this.curIdx,

      timer: null // è®¾ç½®å®æ¶å¨
    }
  },
  watch: {
    // å½å¾çåæ¢ååï¼æåºä¸ä¸ªchangeäºä»¶ä¾ç¨æ·ä½¿ç¨
    currentindex () {
      this.$emit('change', this.currentindex)
    }
  },
  created () {
    this.move()
  },
  methods: {
    // æ§å¶èªå¨è½®æ­çå½æ°----------------------------
    move () {
      if (this.auto) {
        this.timer = setInterval(() => {
          this.hNext()
        }, this.auto)
      }
    },
    // å·¦ä¾§æé®çç¹å»äºä»¶-------------------------
    hPrev () {
      // åæ¢ä¸ä¸å¼ 
      this.currentindex = this.currentindex - 1
      // é²æ­¢è¶ç
      if (this.currentindex === -1) {
        this.currentindex = this.list.length - 1
      }
    },
    // å³ä¾§æé®çç¹å»äºä»¶--------------------------
    hNext () {
      // åæ¢ä¸ä¸å¼ 
      this.currentindex = this.currentindex + 1
      // é²æ­¢è¶ç
      if (this.currentindex === this.list.length) {
        this.currentindex = 0
      }
    },
    // é¼ æ è¿å¥
    mouseEnter () {
      clearInterval(this.timer) // æ¸é¤å®æ¶å¨
    },
    // é¼ æ ç¦»å¼
    mouseLeave () {
      this.move() // éå¯å®æ¶å¨ï¼å¼å§èªå¨è½®æ­
    },
    // æåºäºä»¶,ä¾ç¨æ·ä½¿ç¨---------------------------------
    hClick () {
      this.$emit('click', this.currentindex)
    }
  }
}
</script>

<style>
/* è½®æ­å¾åæ¢æ¶çå¨ç»ææ */
.fade-enter-active,
.fade-leave-active {
  transition: all 0.8s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.slider .slider-content,
.slider img {
  width: 100%;
  height: 100%;
}
.slider {
  margin: 0 auto;
  border: 1px solid #ccc;
  position: relative;
}
.slider .slider-content {
  overflow: hidden;
  position: relative;
}
.slider .slider-content .slider-item {
  position: absolute;
  top: 0;
  left: 0;
}
.slider img {
  width: 100%;
}
.slider .btn,
.slider .txt,
.slider .indirector {
  position: absolute;
}
.slider .btn {
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0);
  top: 50%;
  transform: translateY(-50%);
  transition: background-color 0.2s;
}
.slider .btn:hover {
  background-color: rgba(255, 255, 255, 1);
}

.slider .btn:before,
.slider .btn:after {
  content: "";
  height: 3px;
  width: 25px;
  background-color: #fff;
  position: absolute;
  left: 15px;
  top: 23px;
  transform: rotateZ(60deg);
  transform-origin: 0px center;
  transition: all 0.2s;
}
.slider .btn:after {
  transform: rotateZ(-60deg);
}
.slider .btn:hover:before {
  transform: rotateZ(45deg);
  background-color: red;
}
.slider .btn:hover:after {
  transform: rotateZ(-45deg);
  background-color: red;
}

.slider .btn.btn_right:before,
.slider .btn.btn_right:after {
  transform-origin: right center;
}

.slider .btn.btn_left {
  left: 20px;
}
.slider .btn.btn_right {
  right: 20px;
}

.slider .txt {
  text-indent: 1em;
  line-height: 40px;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: left;
  bottom: 0;
  left: 0;
  width: 100%;
  color: #fff;
}
.slider .indirector {
  bottom: 10px;
  right: 1em;
}
.slider .indirector li {
  display: inline-block;
  margin: 0 5px;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: #fff;
}
.slider .indirector li {
  transition: transform 0.2s;
}
.slider .indirector .current {
  background-color: #369;
  transform: scale(1.2);
}
</style>
