<script type="text/javascript">
export default {
  name: 'MyHeadline',
  // å®ä¹å±æ§æ¥æ¥æ¶ç¨æ·çè¾å¥
  props: {
    level: {
      type: Number, // h1,h2,h3...
      default: 1,
      required: false
    },
    icon: {
      type: String,
      required: false,
      default: '' // é»è®¤æ²¡æå¾æ 
    },
    size: {
      type: String,
      required: false,
      default: 'medium' // é»è®¤ä¸ºmedium
    }
  },
  data () {
    return {}
  },
  render (h) {
    //  <h2 class="ui header">
    //   <i class="settings icon"></i>
    //   <div class="content">
    //     é»è®¤ææ§½çåå®¹
    //     <div class="sub header">å·åææ§½çåå®¹</div>
    //   </div>
    // </h2>
    // level ,icon,size,sub
    // å¤æ­æ¯å¦æicon
    let icon = null
    let content = null

    if (this.icon) {
      icon = h('i', { class: 'icon ' + this.icon })
    }

    // çæç¬¬äºä¸ªå­åç´ content
    // æ¯å¦æå·åææ§½
    if (this.$slots.sub) {
      // æ¯æå¯æ é¢ç
      // åå»ºå¯æ é¢
      const subContent = h('div', { class: 'sub header' }, [this.$slots.sub])
      // åå®¹åå«ä¸¤ä¸ªé¨åï¼é»è®¤ææ§½ + å·åææ§½
      content = h('div', { class: 'content' }, [
        this.$slots.default,
        subContent
      ])
    } else {
      content = h('div', { class: 'content' }, [this.$slots.default])
    }

    // å¾æ i è¦æ¯hæ ç­¾çå­åç´ ï¼æä»¥è¦è®¾ç½®åæ°3
    return h(
      'h' + this.level,
      {
        class: 'ui header ' + this.size
      },
      [icon, content]
    )
  }
}
</script>

